<!-- 个人主页 -->*
<template>
	<view class="content">
		<view class="ph_header dis_flex_c aic ju_c">
			<image :src="getimg('/static/images/logo.png')" mode="widthFix"></image>
			<text>15135725603</text>
		</view>
		<view class="ph_hr"></view>
		<view class="ph_body">
			<view class="ph_body_fl dis_flex ju_b aic">
				<view class="ph_body_fl_item dis_flex_c aic" @click="navQie(index)" v-for="(item,index) in navList"
					:key="index">
					<text :style="{color:item.isActive?'#333':'#666'}">{{item.text}}</text>
					<view v-if="item.isActive"
						style="width: 42upx;height: 5upx;background: #F43B31;border-radius: 3px;">
					</view>
				</view>
			</view>
			<view class="ph_body_sl" v-for="(item,index) in phomepageList" :key="index">
				<view class="ph_body_slfl dis_flex aicx">
					<image :src="getimg(item.img)" mode="aspectFill"></image>
					<view class="oh2">
						<text class="biao">{{item.type}}</text>
						<text class="textBiao">{{item.title}}</text>
					</view>
				</view>
				<view class="ph_body_ll">
					<view class="ph_body_llfl dis_flex ju_b">
						<text class="price">{{item.price}}<text class="fs12 fw400">万</text></text>
						<view class="ph_body_llfl_btn dis_flex ju_b aic">
							<text class="btn_text1">编辑</text>
							<text class="btn_text1" @click.stop="xia(index)">下架</text>
							<text class="btn_text2" @click.stop="del(index)">删除</text>
						</view>
					</view>
				</view>
			</view>

		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				navList: [{
						text: '已发布',
						isActive: true
					},
					{
						text: '已售出',
						isActive: false
					},
					{
						text: '已下架',
						isActive: false
					}
				],
				phomepageList: [{
						type: '商标转让',
						title: '华南地区+卫浴用品+拼音中文商标 已下证',
						img: '/static/images/lzc/fuIndex/shan.png',
						price: '19.00'
					},
					{
						type: '商标转让',
						title: '华南地区+卫浴用品+拼音中文商标 已下证',
						img: '/static/images/lzc/fuIndex/shan.png',
						price: '19.00'
					}
				]
			}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			del(index) {
				uni.showModal({
					title: '你确定要删除吗',
					success: function(res) {
						if (res.confirm) {
							uni.showToast({
								title: '删除成功'
							})
						} else if (res.cancel) {
							uni.showToast({
								title: '取消删除',
								icon:'none'
							})
						}
					}
				});
			},
			xia(index) {
				uni.showModal({
					title: '你确定要下架该商品吗',
					success: function(res) {
						if (res.confirm) {
							uni.showToast({
								title:'下架成功'
							})
						} else if (res.cancel) {
							uni.showToast({
								title:'取消下架',
								icon:'none'
							})
						}
					}
				});
			},
			navQie(index) {
				for (let i = 0; i < this.navList.length; i++) {
					if (i == index) {
						this.navList[i].isActive = true;
					} else {
						this.navList[i].isActive = false;
					}

				}
			}
		}
	}
</script>

<style scoped lang="scss">
	.content {
		background-color: #fff;
		width: 100vw;

		.ph_header {
			width: 750upx;
			height: 272upx;
			background-image: linear-gradient(to right, #F43B31, #FC686F);

			image {
				width: 120upx;
				height: 120upx;
				border-radius: 50%;
			}

			text {
				font-size: 32upx;
				font-family: PingFang SC;
				font-weight: 400;
				color: #FFFFFF;
				margin-top: 31upx;
			}
		}

		.ph_hr {
			width: 750upx;
			height: 50upx;
			background-image: linear-gradient(to right, #F43B31, #FC686F);
		}

		.ph_body {
			width: 750upx;
			// height: 100vh;
			margin-top: -40upx;
			background-color: #fff;
			border-radius: 20upx 20upx 0upx 0upx;

			.ph_body_fl {
				width: 100%;
				height: 88upx;
				box-sizing: border-box;
				padding: 0 118upx;
				background-color: #fff;
				border-radius: 20upx 20upx 0upx 0upx;
				border-bottom: 1px solid #eee;
			}

			.ph_body_sl {
				width: 690upx;
				min-height: 181upx;
				margin: 0 30upx;
				border-bottom: 1px solid #eee;
				margin-top: 22upx;

				.ph_body_slfl {
					width: 100%;
					min-height: 87upx;

					image {
						width: 87upx;
						height: 87upx;
						border-radius: 18upx;
						min-width: 87upx;
					}

					view {
						box-sizing: border-box;
						padding-left: 10upx;

						.biao {
							padding: 7upx;
							background: #B7D0F8;
							border-radius: 4upx;
							font-size: 22upx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #FFFFFF;
							display: inline-block;
							min-width: 100upx;
							text-align: center;
						}

						.textBiao {
							font-size: 28upx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #333333;
							line-height: 40upx;
						}
					}
				}
			}

			.ph_body_ll {
				margin-top: 21upx;

				.price {
					font-size: 36upx;
					font-family: Akzidenz-Grotesk BQ Condensed A;
					font-weight: bold;
					color: #F43B31;
				}

				.ph_body_llfl_btn {
					width: 240upx;
					font-size: 26upx;
					font-family: PingFang SC;
					font-weight: 400;
					line-height: 36upx;

					.btn_text1 {
						color: #F43B31;
					}

					.btn_text2 {
						color: #999999;
					}
				}
			}
		}
	}
</style>
